'use client';

import React , { useState } from "react";
import { Input } from "antd";
import PageLayout from "@/components/layout/PageLayout";
import ModalAlert from "@/components/modal/ModalAlert";
import { useRouter } from 'next/navigation';

export default function Landing() {
    const router = useRouter();

    const [name, setName] = useState<string>('');
    const [isModalOpen, setIsModalOpen] = useState<boolean>(false);


    const onClickButton = () => {
        if (name.trim() !== '') {
            localStorage.setItem('nickname', name.trim());
            router.push('/Home');
        } else {
            setIsModalOpen(true);
        }
    };

    return (
        <>
        
            <PageLayout 
                textButton="เข้าเล่น" 
                styleButton={{
                    backgroundColor: '#FFC124',
                    color: '#FFFFFF',
                    fontWeight: 'bold',
                    fontSize: '20px',
                    fontFamily: 'Kanit, sans-serif',
                    borderRadius: '100px',
                    height: '48px',
                    width: '100%' 
                }}
                onClickButton={onClickButton}
            >
                <div className="flex flex-col w-full h-full md:max-w-2xl items-center justify-center bg-white p-4">
                    <div className="w-full text-left md:text-center">
                        <h1 className="text-black text-[32px] font-medium">Nextzy Test (Full Stack)</h1>
                        <p className="text-[#979797] font-normal text-[14px]">เกมสะสมคะแนน</p>
                    </div>

                    <div className="flex flex-col w-full text-left mt-[30px] md-w-[400px]">
                        <label htmlFor="name" className="text-[#979797] font-normal text-[14px] mb-[8px]">ชื่อสำหรับเล่น (Nickname)</label>
                        <Input
                            id="name"
                            placeholder=""
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            className="w-full h-[40px] rounded-[8px] border border-[#E6E6E6] mt-[8px] mb-[24px] font-semibold text-[16px] !text-[#565656]"
                        />
                    </div>
                </div>
            </PageLayout>

            { isModalOpen && 
                <ModalAlert 
                    onClose={() => setIsModalOpen(false)}
                    title="ผิดพลาด"
                    description="กรุณากรอกชื่อสำหรับเล่น (Nickname) ก่อนเข้าเล่นเกม"
                    width={340}
                /> 
            }
        </>
    );
}